				"html": "<h1>Git Resources</h1>\n<p>A collection of resources on the <a href=\"https://git-scm.com\">Git VCS</a>.</p>\n<p>Git is a free and open source distributed version control system designed to handle everything from small to very large projects with speed and efficiency.</p>\n<h2>Articles</h2>\n<ul>\n<li><a href=\"http://forum.freecodecamp.com/t/wiki-git-aliases/13155\">Aliases</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/amending-the-most-recent-commit-message/13186\">Amend Last</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/git-behind-a-proxy-server/13187\">Git Behind a Proxy</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/find-the-culprit-with-git-blame/13194\">Blame</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/change-the-url-of-a-remote-repository/13196\">Change the URL of a remote repository</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/checkout-a-remote-branch-in-git/13198\">Checkout a remote Branch</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/cloning-all-remote-branches-in-git/13202\">Clone all remote branches</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/cloning-a-specific-branch-in-git/13199\">Clone a specific branch</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/writing-good-git-commit-messages/13210\">Writing Good Git Commit Messages</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/delete-a-git-branch-both-locally-and-remotely/13211\">Delete a branch both locally and remotely</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/discard-unstaged-changes-in-git/13214\">Discard unstaged changes</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/understand-how-to-use-git-merge/13215\">Merge</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/override-local-files-with-git-pull/13216\">Overwrite local files on pull</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/git-pull-vs-git-fetch/13219\">Pull vs Fetch</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/push-a-new-local-branch-to-a-remote-git-repository-and-track-it-too/13222\">Push local to remote repository</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/how-to-use-git-rebase/13226\">Rebase</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/how-to-remove-a-submodule-in-git/13228\">Remove Submodules</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/rename-local-branches-in-git/13230\">Rename Local Branch</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/how-to-squash-multiple-commits-into-one-with-git/13231\">Squash commits</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/how-to-undo-a-git-add/13237\">Undo Git Add</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/when-to-undo-redo/13233\">Undo and Redo A Commit</a></li>\n<li><a href=\"http://forum.freecodecamp.com/t/untrack-files-previously-committed-from-new-gitignore/13239\">Untrack Files</a></li>\n</ul>\n<h2>Books and Tutorials</h2>\n<ul>\n<li><a href=\"https://git-scm.com/book\">Pro Git</a> - The entire Pro Git book, written by Scott Chacon and Ben Straub and published by Apress, is available here. Download this book in PDF, mobi, or ePub form for free.</li>\n<li><a href=\"https://www.atlassian.com/git/tutorials/\">Become a git guru - Atlassian</a> - Responsive site with Git tutorials, training and articles.</li>\n<li><a href=\"http://cbx33.github.io/gitt/\">Git In The Trenches</a> - Git In The Trenches, or GITT is designed to be a book that focusses on teaching people to use Git by associating with scenarios that are experienced by a fictional company called Tamagoyaki Inc. Download this book in PDF, mobi, or ePub form for free.</li>\n<li><a href=\"https://git-scm.com/docs/gittutorial\">Official Git Tutorial</a> - This tutorial explains how to import a new project into Git, make changes to it, and share changes with other developers.</li>\n<li><a href=\"https://git-scm.com/docs/user-manual.html\">Official Git User Manual</a> - This manual is designed to be readable by someone with basic UNIX command-line skills, but no previous knowledge of Git.</li>\n<li><a href=\"https://try.github.io\">Try Git Tutorial by Github and CodeSchool</a> - This tutorial is a quick 15 minutes sprint to get started with Git within the browser.</li>\n</ul>\n<h2>Other Resources</h2>\n<ul>\n<li><a href=\"http://gitready.com\">Git Ready</a> - 'Learn git one commit at a time' by Nick Quaranto</li>\n<li><a href=\"https://hub.github.com/\">Hub</a> - Hub is a command-line wrapper for git that makes you better at GitHub.</li>\n</ul>",
